import Link from "next/link";
import scss from "./StudentsMainContent.module.scss";
import { useGetGimnasiumClassQuery } from "@/redux/api/gimnasium_class";

const StudentsMainContent = () => {
    // const { data, isLoading, isError } = useGetGimnasiumClassQuery();
    const numbers = [4, 5, 6, 7, 8, 9, 10, 11];

    if (isLoading) return <div>Загрузка...</div>;
    if (isError || !data) return <div>Ошибка при загрузке данных.</div>;

    return (
        <section className={scss.content}>
            <div className={scss.classes}>
                {data.map((item, index) => (
                    <Link
                        key={index + 4}
                        href={`/students/studentsclass/${item.name_of_grade.grade}`}
                        className={scss.block}
                    >
                        <h1 className={scss.block_text}>{} класс</h1>
                    </Link>
                ))}
            </div>
        </section>
    );
};

export default StudentsMainContent;
